import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

import { connect } from 'react-redux'
import { loadAll } from './actions'
import { withStyles } from '@material-ui/core/styles'
import Project from './Project'
import Course from './Course'
import Research from './Research'
import Event from './Event'

// tabs
import SwipeableViews from 'react-swipeable-views';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';

//hero
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';

import './logo/github.css'
import './logo/linkedin.css'


function TabContainer({ children, dir }) {
  return (
    <Typography component="div" dir={dir} style={{ padding: 8 * 3 }}>
      {children}
    </Typography>
  );
}

const styles = theme => ({
  rootPaper: {
    backgroundColor: theme.palette.background.paper,
    maxWidth: 800,
    margin: '0 auto'
  },
  root: {
    flexGrow: 1,
  },
  heroUnit: {
    backgroundColor: theme.palette.background.paper,
  },
  heroContent: {
    maxWidth: 600,
    margin: '0 auto',
    padding: `${theme.spacing.unit * 8}px 0 ${theme.spacing.unit * 2}px`,
  }    
});


const tabs = ["Events", "Active Projects", "Research", "Study"]

class App extends Component {

  componentWillMount(){
    this.props.loadAll();
  }

  state = {
    value: 0,
  };

  handleChange = (event, value) => {
    this.setState({ value });
  };

  handleChangeIndex = index => {
    this.setState({ value: index });
  };

  render() {
    const { projects, courses, events, papers, classes, theme } = this.props;
    const { value } = this.state
    return (
      <div className={classes.root}>
        <div className={classes.heroUnit}>
          <div className={classes.heroContent}>
            <Typography component="h1" variant="h2" align="center" color="textPrimary" gutterBottom>
              Hi! I'm Lucas
            </Typography>
            <Typography variant="h6" align="center" color="textSecondary" paragraph>
              And welcome to my personal website. I am a very curious and self-motivated individual with a passion for learning. I am currently looking for opportunities to do high-end research in reinforcement learning. In the tabs below you can find out a bit more about me and what I've done in the last few years.
            </Typography>
        </div>
        <div className={classes.rootPaper}>
          <AppBar position="static" color="default">
            <Tabs
              value={this.state.value}
              onChange={this.handleChange}
              indicatorColor="primary"
              textColor="primary"
              variant="fullWidth"
            >
              <Tab label="Education" />
              <Tab label="Research" />
              <Tab label="Active Projects" />
              <Tab label="Events" />
            </Tabs>
          </AppBar>
          <SwipeableViews
            axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
            index={this.state.value}
            onChangeIndex={this.handleChangeIndex}
          >
            <TabContainer dir={theme.direction}>
             {courses && courses.map(course => (
                <Course key={course.title} details={course} />
              ))}          
            </TabContainer>
            <TabContainer dir={theme.direction}>
             {papers && papers.map(paper => (
                <Research key={paper.title} details={paper} />
              ))}          
            </TabContainer>
            <TabContainer dir={theme.direction}>
              {projects && projects.map(proj => (
                <Project key={proj.title} details={proj} />
              ))}
            </TabContainer>
            <TabContainer dir={theme.direction}>
              {events && events.map(event => (
                <Event key={event.title} details={event} />
              ))}
            </TabContainer>
          </SwipeableViews>
        </div>
      </div>
      <div className={classes.heroButtons}>
        <Grid container spacing={32} justify="center">
          <Grid item>
            <a href="https://www.linkedin.com/in/lucasosouza/" target="_blank" >
            <div className="logo-linkedin-small">
              <div className="i-dot"></div>
              <div className="i-body"></div>
              <div className="n-body"></div>
              <div className="n-curve"></div>
              <div className="n-curve-none"></div>
            </div>
            </a>
          </Grid>
          <Grid item>
            <a href="https://github.com/lucasosouza" target="_blank" >
            <div className="logo-github-small">
              <div className="ear-one-left"></div>
              <div className="ear-one-none"></div>
              <div className="ear-one-right"></div>
              <div className="ear-two-right"></div>
              <div className="ear-two-none"></div>
              <div className="ear-two-left"></div>
              <div className="body-bottom"></div>
              <div className="body-left-none"></div>
              <div className="body-right-none"></div>
              <div className="tail-end"></div>
              <div className="tail-body"></div>
              <div className="tail-body-spots"></div>
              <div className="tail-end-none"></div>
              <div className="tail-body-none"></div>
              <div className="tail-tip"></div>
              <div className="body-left"></div>
              <div className="body-right"></div>
              <div className="head"></div>
              <div className="chin-left"></div>
              <div className="chin-right"></div>
            </div> 
            </a> 
          </Grid>
        </Grid>
      </div>
    </div>
    );
  }
}

const mapStateToProps = (state) => ({
  projects: state.projects,
  courses: state.courses,
  events: state.events,
  papers: state.papers,  
})

const mapDispatchToProps = {
  loadAll
} 


export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles, { withTheme: true })(App));


