import {
  LOAD_DATA
} from './actions'


export const reducer = (state = {projects: [], courses: [], events: [], papers: []}, action) => {

  let type, projects, courses, events, papers;
  ({type, projects, courses, events, papers} = action)

  switch (type) {
    case LOAD_DATA:
      return {
        projects,
        courses,
        events,
        papers
      }
    default:
      return state
  }

}

