import React, { Component } from 'react';
import './App.css';

import { withStyles } from '@material-ui/core/styles';

// expansion panel
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

// chips
import Chip from '@material-ui/core/Chip';
import Paper from '@material-ui/core/Paper';
import TagFacesIcon from '@material-ui/icons/TagFaces';


const styles = theme => ({

  root: {
    width: '100%',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
  chip: {
    margin: theme.spacing.unit,
  },
});


function Course(props) {

    const { details, classes, theme } = props;
    return (
        <div className={classes.root}>
          <ExpansionPanel>
            <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
              <Typography className={classes.heading}>{ details.title }</Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails>
              <Typography>
                { details.desc }
                {details.institution && 
                  <p>Where: {details.institution}, {details.month}/{details.year}</p>}
                { details.repo && 
                  <p>Code: <a href={details.repo}>{details.repo}</a></p>
                }
                { details.classes &&
                <div>
                  <p>Courses: </p>
                  <ul> 
                  { details.classes.map(klass => (
                    <li key={klass}>{klass}</li>
                  )) }
                  </ul>
                </div>
                }
                <p>
                { details.tech && details.tech.map( tc => (
                    <Chip
                      key={tc}
                      label={tc}
                      className={classes.chip}
                      color="primary"
                    />
                ))}                 
                </p>                
              </Typography>

            </ExpansionPanelDetails>
          </ExpansionPanel>
        </div>
    );
}


export default withStyles(styles)(Course);