import React, { Component } from 'react';
import './App.css';

import { withStyles } from '@material-ui/core/styles';

// expansion panel
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

// chips
import Chip from '@material-ui/core/Chip';
import Paper from '@material-ui/core/Paper';
import TagFacesIcon from '@material-ui/icons/TagFaces';


const styles = theme => ({

  root: {
    width: '100%',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
  chip: {
    margin: theme.spacing.unit,
  },
});


function Research(props) {

    const { details, classes, theme } = props;
    return (
        <div className={classes.root}>
          <ExpansionPanel>
            <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
              <Typography className={classes.heading}>{ details.title }</Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails>
              <Typography>
                { details.desc }
                { details.publications &&
                <div>
                  <p>Publications: </p>
                  <ul> 
                  { details.publications.map(paper => (
                    paper.link ? 
                    <li key={paper.title}><a href={paper.link}>{paper.title}. {paper.published_at}</a></li>
                    : 
                    <li key={paper.title}>{paper.title} ({paper.published_at}).</li>
                  )) }
                  </ul>
                </div>
                }
                <p>
                { details.tech && details.tech.map( tc => (
                    <Chip
                      key={tc}
                      label={tc}
                      className={classes.chip}
                      color="primary"
                    />
                ))}                 
                </p>                
              </Typography>

            </ExpansionPanelDetails>
          </ExpansionPanel>
        </div>
    );
}


export default withStyles(styles)(Research);